
.menu-bar {
  width: 100%;
  height: auto;
  position: fixed;
  padding: 1em 1em 1em 1em;
  display: flex;
  flex-direction: row;
}
.menu-bar .router {
    color: black;
    margin: 0;
    display: flex;
    justify-content: center;
}
.menu-bar .description {
    margin: auto 2em auto 0.5em;
    width: 100%;
    text-align: center;
}
.light {
  background: #F5F5F5;
  color: #212121;
  border-bottom: solid 1px #BDBDBD;
}
.light .hamburger {
    background: url("./assets/hamburger-light.svg");
    width: 20px;
    height: 20px;
    margin: 0;
    padding: 0;
    border: none;
    background-size: contain;
    background-repeat: no-repeat;
}
.dark {
  background: #000000;
  color: #EEE;
  border-bottom: solid 1px #424242;
}
.dark .hamburger {
    background: url("./assets/hamburger-dark.svg");
    width: 20px;
    height: 20px;
    margin: 0;
    padding: 0;
    border: none;
    background-size: contain;
    background-repeat: no-repeat;
}



