
.calendar main .wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  overflow: auto;
  white-space: nowrap;
}
.calendar main .wrapper .day {
    margin: 0;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    cursor: pointer;
}
.calendar main .wrapper .day .icon {
      font-size: 2em;
      padding: 0 0 0.25em 0;
      margin: auto auto;
}
.calendar main .wrapper .day p {
      font-size: 1.2em;
      padding: 0.25em 0 0 0;
      margin: auto auto;
}
.calendar main .wrapper .day .active {
      display: grid;
      grid-template-rows: 1fr 1fr 1fr 1fr;
      background: #3F51B5;
      color: #F5F5F5;
      width: 26vw;
      height: 26vw;
}
.calendar main .wrapper .day .not-active {
      display: grid;
      grid-template-rows: 1fr 1fr 1fr 1fr;
      background: none;
      width: 26vw;
      height: 26vw;
}
.light {
  background: #F5F5F5;
  color: #212121;
}
.dark {
  background: #000000;
  color: #EEE;
}



