
.menu-bar {
  width: 100%;
  height: auto;
  padding: 1em 1em 1em 1em;
  position: fixed;
  display: flex;
  flex-direction: row;
}
.menu-bar .back-btn {
    width: 20px;
    height: 20px;
    margin: 0;
    padding: 0;
    border: none;
}
.menu-bar .router {
    margin: 0;
    color: black;
    display: flex;
    justify-content: center;
}
.menu-bar .description {
    width: 100%;
    margin: auto 2em auto 0.5em;
    text-align: center;
}
.light {
  background: #F5F5F5;
  border-bottom: solid 1px #BDBDBD;
}
.light .back-btn {
    background: url("./assets/back-arrow-light.svg");
    background-size: contain;
    background-repeat: no-repeat;
}
.dark {
  background: black;
  border-bottom: solid 1px #424242;
  color: #F5F5F5;
}
.dark .back-btn {
    background: url("./assets/back-arrow-dark.svg");
    background-size: contain;
    background-repeat: no-repeat;
}



